.list {
  display: flex;
  justify-content: space-around;
  margin: 0 auto;
  border-bottom: 1px solid #3333;
  min-width: 396px;
}

.link {
  display: inline-block;
  text-decoration: none;
  padding: 12px;
  font-weight: 500;
  color: #c9d1d9;
  font-size: 20px;
}
.link:hover,
.link:focus {
  color: #3f51b5;
}

.activeLink {
  composes: link;
  color: #3f51b5;
}
